.watch {
  width: 100vw;
  height: 100vh;
  position: relative;
  .back {
    display: flex;
    align-items: center;
    position: absolute;
    top: 10px;
    left: 10px;
    color: white;
    cursor: pointer;
    z-index: 2;
    font-size: 20px;
    font-weight: 600;
  }

  .video {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
  }
}
